.container {
  margin: calc(1vh + 5% - 20px) auto calc(2vh - 20px);
  width: 600px; }
  @media screen and (orientation: portrait) and (max-width: 440px) {
    .container {
      width: 390px; } }
  .container main {
    position: relative;
    min-width: 240px;
    padding: 20px;
    background-color: var(--bg-color);
    color: var(--fg-color);
    display: table-cell; }
