footer {
  padding: 20px;
  font-size: 0.8rem; }
  footer .actionSpinner {
    margin-left: 3px;
    margin-top: 2px;
    display: none; }
    footer .actionSpinner.activeSpinner {
      display: block; }
  footer p {
    margin: 10px 0 0;
    color: gray !important; }
    footer p a {
      color: inherit;
      text-decoration: none; }
      footer p a:focus, footer p a:hover {
        color: var(--fg-color); }
    footer p.version {
      opacity: .5;
      margin-top: 5px;
      user-select: none; }
